'use client';

import { useState, useEffect, useRef } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';

interface LocationData {
  lat: number;
  lng: number;
}

export default function QRScannerPage() {
  const [isScanning, setIsScanning] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [qrInput, setQrInput] = useState('');
  const videoRef = useRef<HTMLVideoElement>(null);
  const qrScannerRef = useRef<any>(null);

  useEffect(() => {
    initializeScanner();
    return () => {
      stopScanner();
    };
  }, []);

  const initializeScanner = async () => {
    try {
      setError(null);
      
      // Dynamically import QrScanner to avoid SSR issues
      const QrScanner = (await import('qr-scanner')).default;
      
      if (videoRef.current) {
        qrScannerRef.current = new QrScanner(
          videoRef.current,
          (result) => {
            console.log('QR Code detected:', result.data);
            handleQRDetected(result.data);
          },
          {
            onDecodeError: (err) => {
              // Ignore decode errors as they're normal when no QR code is visible
              console.log('Decode error (normal):', err);
            },
            highlightScanRegion: true,
            highlightCodeOutline: true,
          }
        );

        await qrScannerRef.current.start();
        setIsScanning(true);
      }
    } catch (err) {
      setError('Failed to initialize QR scanner. Please check camera permissions.');
      console.error('Scanner initialization error:', err);
    }
  };

  const stopScanner = () => {
    if (qrScannerRef.current) {
      qrScannerRef.current.stop();
      qrScannerRef.current.destroy();
      qrScannerRef.current = null;
    }
    setIsScanning(false);
  };

  const startScanner = async () => {
    if (qrScannerRef.current) {
      try {
        await qrScannerRef.current.start();
        setIsScanning(true);
        setError(null);
      } catch (err) {
        setError('Failed to start scanner. Please check camera permissions.');
        console.error('Scanner start error:', err);
      }
    } else {
      initializeScanner();
    }
  };

  const getCurrentLocation = (): Promise<LocationData> => {
    return new Promise((resolve, reject) => {
      if (!navigator.geolocation) {
        reject(new Error('Geolocation is not supported by this browser'));
        return;
      }

      navigator.geolocation.getCurrentPosition(
        (position) => {
          resolve({
            lat: position.coords.latitude,
            lng: position.coords.longitude,
          });
        },
        (error) => {
          reject(new Error(`Location access denied: ${error.message}`));
        },
        {
          enableHighAccuracy: true,
          timeout: 10000,
          maximumAge: 60000,
        }
      );
    });
  };

  const submitAttendance = async (qrData: string) => {
    setIsSubmitting(true);
    setError(null);

    try {
      // Get current location
      const location = await getCurrentLocation();

      // Submit attendance
      const response = await fetch('/api/attendance', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          qrData,
          location,
        }),
      });

      const result = await response.json();

      if (!response.ok) {
        throw new Error(result.error || 'Failed to submit attendance');
      }

      setSuccess(`Attendance recorded successfully! Time: ${new Date().toLocaleString()}`);
      stopScanner();
      
      // Reset after 3 seconds
      setTimeout(() => {
        setSuccess(null);
        setQrInput('');
        startScanner();
      }, 3000);

    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred');
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleManualSubmit = () => {
    if (qrInput.trim()) {
      submitAttendance(qrInput.trim());
    }
  };

  const handleQRDetected = (qrData: string) => {
    // This would be called by a QR scanning library
    // For demo purposes, we'll use manual input
    submitAttendance(qrData);
  };

  return (
    <div className="min-h-screen bg-background p-4">
      <div className="max-w-2xl mx-auto">
        <Card className="mb-6">
          <CardHeader className="text-center">
            <CardTitle className="text-2xl font-bold">QR Code Scanner</CardTitle>
            <CardDescription>
              Scan a QR code to record your attendance
            </CardDescription>
          </CardHeader>
          <CardContent>
            {error && (
              <Alert variant="destructive" className="mb-4">
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}

            {success && (
              <Alert className="mb-4 border-green-200 bg-green-50 text-green-800">
                <AlertDescription>{success}</AlertDescription>
              </Alert>
            )}

            {/* Camera View */}
            <div className="relative mb-6">
              <video
                ref={videoRef}
                autoPlay
                playsInline
                muted
                className="w-full h-64 bg-black rounded-lg object-cover"
              />
              
              {!isScanning && !error && (
                <div className="absolute inset-0 flex items-center justify-center bg-black bg-opacity-50 rounded-lg">
                  <p className="text-white text-center">Starting camera...</p>
                </div>
              )}

              {isScanning && (
                <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
                  <div className="border-2 border-white border-dashed w-48 h-48 rounded-lg flex items-center justify-center">
                    <p className="text-white text-center text-sm">
                      Align QR code within this frame
                    </p>
                  </div>
                </div>
              )}
            </div>

            {/* Manual QR Input (for demo purposes) */}
            <div className="space-y-4">
              <div className="text-center text-sm text-muted-foreground">
                <p>For demo purposes, you can manually enter QR code data:</p>
              </div>
              
              <div className="flex gap-2">
                <input
                  type="text"
                  value={qrInput}
                  onChange={(e) => setQrInput(e.target.value)}
                  placeholder="Enter QR code data (e.g., ATTENDANCE_001)"
                  className="flex-1 px-3 py-2 border border-input rounded-md bg-background"
                  disabled={isSubmitting}
                />
                <Button
                  onClick={handleManualSubmit}
                  disabled={!qrInput.trim() || isSubmitting}
                >
                  {isSubmitting ? 'Submitting...' : 'Submit'}
                </Button>
              </div>
            </div>

            {/* Scanner Controls */}
            <div className="flex gap-2 mt-4">
              <Button
                onClick={startScanner}
                disabled={isScanning || isSubmitting}
                variant="outline"
                className="flex-1"
              >
                Start Scanner
              </Button>
              <Button
                onClick={stopScanner}
                disabled={!isScanning || isSubmitting}
                variant="outline"
                className="flex-1"
              >
                Stop Scanner
              </Button>
            </div>
          </CardContent>
        </Card>

        <div className="text-center text-sm text-muted-foreground">
          <p>Make sure to allow camera and location permissions for proper functionality.</p>
        </div>
      </div>
    </div>
  );
}
